import poof from '../behaviors/confetti'
import {controller, target} from '@github/catalyst'

@controller
class ShowConfettiPartyElement extends HTMLElement {
  connectedCallback() {
    poof()
  }
}

@controller
class BillingDurationElement extends HTMLElement {
  @target yearlyRadioInput: HTMLElement
  @target monthlyRadioInput: HTMLElement

  toggleHighlight(add: HTMLElement, remove: HTMLElement) {
    add.classList.add('color-bg-inset', 'color-border-accent')
    remove.classList.remove('color-bg-inset', 'color-border-accent')
  }

  select(event: Event) {
    if (event.currentTarget === this.yearlyRadioInput) {
      this.toggleHighlight(this.yearlyRadioInput, this.monthlyRadioInput)
    } else if (event.currentTarget === this.monthlyRadioInput) {
      this.toggleHighlight(this.monthlyRadioInput, this.yearlyRadioInput)
    }
  }
}
