// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'
import {onKey} from '@github-ui/onfocus'
import {debounce} from '@github/mini-throttle'
import {TemplateInstance} from '@github/template-parts'
import {parseHTML} from '@github-ui/parse-html'
import {addCopilotHiddenFormElement, removeHiddenFormElements, handleCopilotAllCheckbox} from './cfb-settings'

on('click', '.js-copilot-update-individual-org-enablement', async function (event) {
  const target = event.currentTarget as HTMLElement
  const form = <HTMLFormElement>target.closest('form')
  const body = new FormData(form)

  body.append('enablement', target.getAttribute('data-value') || '')

  const response = await fetch(form.action, {
    method: 'PUT',
    body,
    headers: {Accept: 'text/fragment+html'},
  })

  if (response.status >= 400) {
    // eslint-disable-next-line i18n-text/no-en
    const message = 'An unknown error occurred.'
    const template = document.querySelector<HTMLTemplateElement>('template.js-flash-template')!
    template.after(new TemplateInstance(template, {className: 'flash-error', message}))
  } else {
    const replaceTarget = <HTMLElement>form.closest('[data-replace-remote-form-target]')
    const partial = parseHTML(document, await response.text())
    replaceTarget.replaceWith(partial)
    target.setAttribute('aria-checked', 'true')
    updateEnabledCount(target.getAttribute('data-value') === 'enable' ? 1 : -1)
  }
})

on('click', '.js-copilot-select-all-orgs', function (event) {
  const target = event.target as HTMLInputElement

  const checkboxes = document.querySelectorAll<HTMLInputElement>('.js-copilot-org-bulk-actions-checkbox')
  let count = 0
  for (const checkbox of checkboxes) {
    // Orgs on an enterprise trial or those without copilot enable shouldn't be selectable
    if (checkbox.disabled) {
      continue
    }

    if (target.checked) {
      checkbox.checked = true
      const bulkActions = document.getElementById('enterprise-management-bulk-menu')
      bulkActions!.hidden = false
      count += 1
      const defaultTitle = document.getElementById('copilot-org-select-default-header')
      defaultTitle!.textContent = `${count} ${pluralize(count)} selected`

      trackCheckedValues(checkbox)
    } else {
      const bulkActions = document.getElementById('enterprise-management-bulk-menu')
      bulkActions!.hidden = true
      const defaultTitle = document.getElementById('copilot-org-select-default-header')
      defaultTitle!.textContent = `${count} ${pluralize(count)} selected`
      checkbox.checked = false
    }
  }
})

on('click', '.js-disable-copilot-orgs', async function (event) {
  const target = event.target as HTMLElement
  const bulkForm = <HTMLFormElement>target.closest('form')
  const checkboxes = document.querySelectorAll<HTMLInputElement>('.js-copilot-org-bulk-actions-checkbox')
  for (const checkbox of checkboxes) {
    if (checkbox.checked) {
      addCopilotHiddenFormElement(bulkForm, checkbox.name, checkbox.value)
    }
  }
  addCopilotHiddenFormElement(bulkForm, 'enablement', 'disable')
  const form = <HTMLFormElement>target.closest('form')
  const body = new FormData(form)

  const urlParams = new URLSearchParams(window.location.search)
  if (urlParams.get('page')) {
    body.append('page', urlParams.get('page')!)
  }
  if (urlParams.get('query')) {
    body.append('query', urlParams.get('query')!)
  }

  const response = await fetch(form.action, {
    method: 'PUT',
    body,
    headers: {Accept: 'text/fragment+html'},
  })

  if (response.status >= 400) {
    // eslint-disable-next-line i18n-text/no-en
    const message = 'An unknown error occurred.'
    const template = document.querySelector<HTMLTemplateElement>('template.js-flash-template')!
    template.after(new TemplateInstance(template, {className: 'flash-error', message}))
  } else {
    const bulkActions = document.getElementById('enterprise-management-bulk-menu')
    bulkActions!.hidden = true
    const defaultTitle = document.getElementById('copilot-org-select-default-header')
    // eslint-disable-next-line i18n-text/no-en
    defaultTitle!.textContent = 'Select organizations'
    const allCheckbox = document.getElementById('select-all-checkbox') as HTMLInputElement
    allCheckbox.checked = false
    allCheckbox.indeterminate = false
    bulkUpdateEnabledCount('disable')

    const replaceTarget = document.getElementById('copilot-org-select-list-component')!
    const partial = parseHTML(document, await response.text())
    replaceTarget.replaceWith(partial)
  }
  removeHiddenFormElements()
})

