on('change', '.js-copilot-org-bulk-actions-checkbox', function (event) {
  const checkboxes = document.querySelectorAll<HTMLInputElement>('.js-copilot-org-bulk-actions-checkbox')
  let found = false
  let count = 0
  let totalCount = 0
  for (const checkbox of checkboxes) {
    totalCount += 1
    if (checkbox.checked) {
      const bulkActions = document.getElementById('enterprise-management-bulk-menu')
      bulkActions!.hidden = false
      count += 1
      const defaultTitle = document.getElementById('copilot-org-select-default-header')
      defaultTitle!.textContent = `${count} ${pluralize(count)} selected`

      found = true
    }
  }
  const allCheckbox = document.getElementById('select-all-checkbox') as HTMLInputElement
  handleCopilotAllCheckbox(allCheckbox, count, totalCount)

  if (found === false) {
    const bulkActions = document.getElementById('enterprise-management-bulk-menu')
    bulkActions!.hidden = true
    const defaultTitle = document.getElementById('copilot-org-select-default-header')
    // eslint-disable-next-line i18n-text/no-en
    defaultTitle!.textContent = 'Select organizations'
  }
  trackCheckedValues(<HTMLInputElement>event.target)
})

const checkedValues = new Map<string, string>()
let enabledCount = parseInt(
  document.getElementById('js-copilot-enabled-count')?.getAttribute('data-enabled-count') || '0',
)

const bulkUpdateEnabledCount = (enablement: string) => {
  for (const value of checkedValues.values()) {
    if (value !== enablement) {
      updateEnabledCount(enablement === 'enable' ? 1 : -1)
    }
  }
  checkedValues.clear()
}

const updateEnabledCount = (change: number) => {
  enabledCount += change

  const enabledCountElement = document.getElementById('js-copilot-enabled-count')

  if (!enabledCountElement) {
    return
  }

  if (!enabledCount) {
    enabledCountElement.textContent = ''
  } else {
    enabledCountElement.textContent = `${enabledCount} ${enabledCount === 1 ? 'organization' : 'organizations'}`
  }
}

const trackCheckedValues = (target: HTMLInputElement) => {
  if (target.checked) {
    checkedValues.set(target.getAttribute('data-org-id') || '', target.getAttribute('data-enablement-status') || '')
  } else {
    checkedValues.delete(target.getAttribute('data-org-id') || '')
  }
}

const debouncedSearch = debounce(searchOrgs, 200)
onKey('keyup', '.js-copilot-organization-search', function () {
  debouncedSearch()
})

async function searchOrgs() {
  const input = document.querySelector<HTMLInputElement>('.js-copilot-organization-search')!
  let query
  if (input && input.value) {
    query = input.value
  }

  const urlParams = new URLSearchParams(window.location.search)
  urlParams.set('query', query || '')
  const url = `${window.location.pathname}?${urlParams.toString()}`
  window.history.pushState({}, '', url)
  const form = <HTMLFormElement>input.closest('form')
  const body = new FormData(form)

  const response = await fetch(form.action, {
    method: 'POST',
    body,
    headers: {Accept: 'text/fragment+html'},
  })

  if (response.status >= 400) {
    // eslint-disable-next-line i18n-text/no-en
    const message = 'An unknown error occurred.'
    const template = document.querySelector<HTMLTemplateElement>('template.js-flash-template')!
    template.after(new TemplateInstance(template, {className: 'flash-error', message}))
  } else {
    const replaceTarget = document.getElementById('copilot-org-select-list-component')!
    const partial = parseHTML(document, await response.text())
    replaceTarget.replaceWith(partial)
  }
}

const setStandaloneFormButtonVisibility = (showSubmit: boolean, showDialog: boolean) => {
  const submitButton = document.getElementById('standalone-form-component-submit-button')
  const dialogButton = document.getElementById('dialog-show-copilot-business-settings-access-component')
  submitButton!.hidden = showSubmit
  dialogButton!.hidden = showDialog
}
on('click', '#standalone-form-component-radio-button-disabled', () => setStandaloneFormButtonVisibility(true, false))
on('click', '#standalone-form-component-radio-button-enabled', () => setStandaloneFormButtonVisibility(false, true))

function pluralize(count: number) {
  return count === 1 ? 'organization' : 'organizations'
}
